@import "../variables.scss";

.nav{
    //position: fixed;
    .nav-image {
        width: 350px;
        height: 150px;
}
    a {
        color: white;
        display: inline-flex;
        padding-left: 10%;
        padding-right: 10%;
        font-size: 1.5rem;
        
    }
    a:link {
        text-decoration-line: none;
        cursor: pointer;
        
    }
    a:hover{
        color: rgb(228, 105, 53)
    }
    .logout-button {
        font-family: 'Inter', sans-serif;
        background: $background-color-buttons;
        width: 120px;
        height: 2rem;
        color: $white-font;
        font-size: 20px;
        border-radius: 2rem;
        margin-top: 1.8rem;
        border: none;
        box-shadow: 3px 3px 3px 1px rgb(121, 116, 116);  
    }
    .logout-button:hover{
        background: rgb(9, 67, 13);
    }
    .centered {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    

    @media screen and (max-width: 500px){
    .nav-image {
        display: none;
    }
    
  
}
    // @media screen and (max-width: 800px){
    //     .nav-image {
    //         width: 600px;
    //         height: 350px;
    //     }
    //    }
    @media screen and (min-width: 501px){
            .nav-image {
                display: none;
            }
            .nav-links{
                display: inline-flex;
                justify-content: space-around;
                
            }
           
            }

}
